<script>
  export default {
    data() {
      return {
        chartInstance: null,
        chartData: {
          labels: ['Âë®‰∏Ä', 'Âë®‰∫å', 'Âë®‰∏â', 'Âë®Âõõ', 'Âë®‰∫î', 'Âë®ÂÖ≠', 'Âë®Êó•'],
          values: [3, 4, 5, 4, 6, 3, 4]
        },
        userData: {
          name: 'Â≠¶‰π†ËÄÖ',
          avatar: '../../../static/logo.png',
          slogan: 'Â≠¶‰π†ËøõÊ≠•ÊØè‰∏ÄÂ§©'
        },
        isDarkMode: false,
        notificationEnabled: true,
        selectedThemeIndex: 0,
        themeColors: ['#5374f7', '#722ED1', '#E73D8D', '#00B42A']
      }
    },
    onLoad() {
      // È°µÈù¢Âä†ËΩΩÊó∂ÁöÑÂàùÂßãÂåñÈÄªËæë
    },
    onReady() {
      // È°µÈù¢Ê∏≤ÊüìÂÆåÊàêÂêéÁªòÂà∂ÂõæË°®
      this.drawTrendChart();
    },
    methods: {
      drawTrendChart() {
        // ‰ΩøÁî®uni-appÁöÑcanvas APIÁªòÂà∂ÂõæË°®
        const ctx = uni.createCanvasContext('trendChart', this);
        const width = 300;
        const height = 100;
        
        // ËÆæÁΩÆËÉåÊôØ
        ctx.setFillStyle('#fff');
        ctx.fillRect(0, 0, width, height);
        
        // ËÆæÁΩÆÂõæË°®Ê†∑Âºè
        ctx.setStrokeStyle('#5374f7');
        ctx.setLineWidth(2);
        ctx.setFillStyle('#5374f7');
        
        // ÁªòÂà∂ÁΩëÊ†ºÁ∫ø
        ctx.setStrokeStyle('#f0f0f0');
        ctx.setLineWidth(1);
        
        // Ê®™Á∫ø
        for (let i = 0; i <= 5; i++) {
          const y = 20 + i * 12;
          ctx.beginPath();
          ctx.moveTo(20, y);
          ctx.lineTo(width - 20, y);
          ctx.stroke();
        }
        
        // ÁªòÂà∂ÊäòÁ∫ø
        const padding = 20;
        const pointSpacing = (width - 2 * padding) / (this.chartData.labels.length - 1);
        
        ctx.setStrokeStyle('#5374f7');
        ctx.beginPath();
        this.chartData.values.forEach((value, index) => {
          const x = padding + index * pointSpacing;
          const y = height - padding - (value / 10) * (height - 2 * padding);
          
          if (index === 0) {
            ctx.moveTo(x, y);
          } else {
            ctx.lineTo(x, y);
          }
        });
        ctx.stroke();
        
        // ÁªòÂà∂ÁÇπ
        this.chartData.values.forEach((value, index) => {
          const x = padding + index * pointSpacing;
          const y = height - padding - (value / 10) * (height - 2 * padding);
          
          ctx.beginPath();
          ctx.arc(x, y, 4, 0, 2 * Math.PI);
          ctx.fill();
        });
        
        // ÁªòÂà∂Ê†áÁ≠æ
        ctx.setFontSize(10);
        ctx.setFillStyle('#999');
        this.chartData.labels.forEach((label, index) => {
          const x = padding + index * pointSpacing;
          const y = height - 5;
          ctx.fillText(label, x - 10, y);
        });
        
        ctx.draw();
      },
      // Â§ÑÁêÜÈÄÄÂá∫ÁôªÂΩï
      logout() {
        uni.showModal({
          title: 'Á°ÆËÆ§ÈÄÄÂá∫',
          content: 'Á°ÆÂÆöË¶ÅÈÄÄÂá∫ÂΩìÂâçË¥¶Âè∑ÂêóÔºü',
          success: (res) => {
            if (res.confirm) {
              // Ê∏ÖÈô§Êú¨Âú∞Â≠òÂÇ®ÁöÑÁî®Êà∑Êï∞ÊçÆ
              uni.removeStorageSync('userInfo');
              uni.navigateTo({
                url: '/pages/login/login'
              });
            }
          }
        });
      },
      // Â§ÑÁêÜËÆæÁΩÆÈ°πÁÇπÂáª
      handleSettingClick(type) {
        switch (type) {
          case 'profile':
            uni.showToast({
              title: 'Ë∑≥ËΩ¨Âà∞‰∏™‰∫∫ËµÑÊñôÁºñËæë',
              icon: 'none'
            });
            break;
          case 'security':
            uni.showToast({
              title: 'Ë∑≥ËΩ¨Âà∞Ë¥¶Êà∑ÂÆâÂÖ®',
              icon: 'none'
            });
            break;
          case 'language':
            uni.showToast({
              title: 'Ë∑≥ËΩ¨Âà∞ËØ≠Ë®ÄËÆæÁΩÆ',
              icon: 'none'
            });
            break;
          case 'help':
            uni.showToast({
              title: 'Ë∑≥ËΩ¨Âà∞Â∏ÆÂä©‰∏éÊîØÊåÅ',
              icon: 'none'
            });
            break;
          case 'about':
            uni.showToast({
              title: 'Ë∑≥ËΩ¨Âà∞ÂÖ≥‰∫éÂ∫îÁî®',
              icon: 'none'
            });
            break;
        }
      },
      // ÂàáÊç¢Ê∑±Ëâ≤Ê®°Âºè
      toggleDarkMode(e) {
        this.isDarkMode = e.detail.value;
        // ËøôÈáåÂèØ‰ª•Ê∑ªÂä†Ê∑±Ëâ≤Ê®°ÂºèÁöÑÂÖ∑‰ΩìÂÆûÁé∞
        uni.setStorageSync('darkMode', this.isDarkMode);
        uni.showToast({
          title: this.isDarkMode ? 'Â∑≤ÂºÄÂêØÊ∑±Ëâ≤Ê®°Âºè' : 'Â∑≤ÂÖ≥Èó≠Ê∑±Ëâ≤Ê®°Âºè',
          icon: 'none'
        });
      },
      // ÂàáÊç¢ÈÄöÁü•ËÆæÁΩÆ
      toggleNotification(e) {
        this.notificationEnabled = e.detail.value;
        uni.setStorageSync('notificationEnabled', this.notificationEnabled);
      },
      // ÂàáÊç¢‰∏ªÈ¢òËâ≤
      selectTheme(index) {
        this.selectedThemeIndex = index;
        uni.setStorageSync('themeColor', this.themeColors[index]);
        uni.showToast({
          title: '‰∏ªÈ¢òÂ∑≤ÂàáÊç¢',
          icon: 'none'
        });
      },
      // Ê®°ÊãüÂä†ËΩΩÁî®Êà∑Êï∞ÊçÆ
      loadUserData() {
        // ‰ªéÊú¨Âú∞Â≠òÂÇ®Âä†ËΩΩÊï∞ÊçÆÔºåÂ¶ÇÊûúÊ≤°ÊúâÂàô‰ΩøÁî®ÈªòËÆ§Êï∞ÊçÆ
        const savedUser = uni.getStorageSync('userInfo');
        if (savedUser) {
          this.userData = savedUser;
        }
        
        // Âä†ËΩΩ‰∏ªÈ¢òËÆæÁΩÆ
        const savedTheme = uni.getStorageSync('themeColor');
        if (savedTheme) {
          const index = this.themeColors.indexOf(savedTheme);
          if (index !== -1) {
            this.selectedThemeIndex = index;
          }
        }
        
        // Âä†ËΩΩÊ∑±Ëâ≤Ê®°ÂºèËÆæÁΩÆ
        const darkMode = uni.getStorageSync('darkMode');
        if (darkMode !== null) {
          this.isDarkMode = darkMode;
        }
        
        // Âä†ËΩΩÈÄöÁü•ËÆæÁΩÆ
        const notification = uni.getStorageSync('notificationEnabled');
        if (notification !== null) {
          this.notificationEnabled = notification;
        }
      }
    },
    onLoad() {
      // Âä†ËΩΩÁî®Êà∑Êï∞ÊçÆ
      this.loadUserData();
    }
  }
</script>

